.b-f, .n-form {
  .btn {
    background: $blue;
  }
  .group {
    position: relative;
    margin-bottom: 30px;
  }

  input {
    font-size: 16px;
    padding: 10px;
    display: block;
    width: 100%;
    border: none;
    margin-bottom: 30px;
    border-bottom: 1px solid $blue;
    background: transparent;
  }
  input:focus {
    outline: none;
  }
  /* LABEL ======================================= */
  label {
    color: $black;
    font-size: 18px;
    position: absolute;
    pointer-events: none;
    left: 10px;
    top: 15px;
    transition: 0.2s ease all;
    -moz-transition: 0.2s ease all;
    -webkit-transition: 0.2s ease all;
  }

  /* active state */
  input:focus ~ label, input:valid ~ label {
    top: -15px;
    font-size: 14px;
    //color: #5264AE;
  }


  /* BOTTOM BARS ================================= */
  .bar {
    position: relative;
    display: block;
    width: 100%;
  }
  .bar:before, .bar:after {
    content: "";
    height: 2px;
    width: 0;
    bottom: 0;
    position: absolute;
    //background: #5264AE;
    transition: 0.2s ease all;
    -moz-transition: 0.2s ease all;
    -webkit-transition: 0.2s ease all;
  }
  .bar:before {
    left: 50%;
  }
  .bar:after {
    right: 50%;
  }

  /* active state */
  input:focus ~ .bar:before,
  input:focus ~ .bar:after {
    width: 50%;
  }
}
